#container {
  position: absolute;
  overflow: hidden;
}
.outer {
  position: relative;
  right: -50%;
}

.inner_cont {
  display: grid;
  grid-template: 1fr / 1fr;
  place-items: center;
}
.inner_cont > * {
  grid-column: 1 / 1;
  grid-row: 1 / 1;
}
.inner.top {
  position: relative;
  z-index: 3;
}
.inner.below {
  position: relative;
  z-index: 2;
}
.inner.below2 {
  position: relative;
  z-index: 1;
}
.inner.below3 {
  position: relative;
  z-index: 0;
}
.inner.below4 {
  position: relative;
  z-index: -1;
}
.inner.below5 {
  position: relative;
  z-index: -2;
}
.inner.below6 {
  position: relative;
  z-index: -3;
}
.inner.below7 {
  position: relative;
  z-index: -4;
}
.inner.below8 {
  position: relative;
  z-index: -5;
}
.inner.below9 {
  position: relative;
  z-index: -6;
}
.inner.below10 {
  position: relative;
  z-index: -7;
}

.no-rot {
  position: relative;
  right: -50%;
}
.rotated-image {
  -webkit-animation-name: spin;
  -webkit-animation-duration: 2000ms;
  -webkit-animation-iteration-count: 1;
  -webkit-animation-timing-function: linear;
  -moz-animation-name: spin;
  -moz-animation-duration: 2000ms;
  -moz-animation-iteration-count: 1;
  -moz-animation-timing-function: linear;
  position: relative;
  right: -50%;
}
@-moz-keyframes spin {
  from {
    -moz-transform: rotate(0deg);
  }
  to {
    -moz-transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.rotated-image-clockWise {
  position: relative;
  right: -50%;
  -webkit-animation: rotation 2s linear;
}

@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(-359deg);
  }
}
